import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			couponList: [{
					title: '新用户专享优惠券',
					price: 5,
				},
				{
					title: '庆五一发一波优惠券',
					price: 10,
				},
				{
					title: '优惠券优惠券优惠券优惠券',
					price: 15,
				}
			],
			addressData: {
				name: '许小星',
				mobile: '13853989563',
				addressName: '金九大道',
				address: '山东省济南市历城区',
				area: '149号',
				default: false,
			},
			goodsList: [],
			paymentList: [],
			totalAmount: 0,
			order_info: {
				jsondata: '[{ "article_id":0,"price":0,"spec_text":"","img_url":"","title":"", "goods_id":0, "quantity":0, "hot_id":0}]',
				payment_id: 0,
				accept_name: '',
				mobile: '',
				area: '',
				book_id: 0,
				address: ''
			},
			switchA:false
		}
	},
	computed: {
		...mapState(['hasLogin', 'userInfo', 'bi'])
	},
	onLoad(option) {

		let that = this;
		console.log(that.userInfo.id);
		uni.request({
			url: common.get_user_addr_bookUrl, //仅为示例，并非真实接口地址。
			data: {
				is_mobile: 1,
				user_id: that.userInfo.id
			},
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: function(res) {
				if (res.data.status == 1) {
					console.log(JSON.stringify(res.data.data))
					that.addressData = res.data.data[0];

					that.order_info.accept_name = that.addressData.accept_name;
					that.order_info.mobile = that.addressData.mobile;
					that.order_info.area = that.addressData.province + that.addressData.city + that.addressData.area;
					that.order_info.book_id = that.addressData.id;
					that.order_info.address = that.addressData.addressName;



				} else {



				}



			}
		});
		that = this;
		uni.request({
			url: common.get_cart_itemsUrl, //仅为示例，并非真实接口地址。
			data: {
				is_mobile: 1,
				user_id: that.userInfo.id
			},
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: function(res) {
				if (res.data.status == 1) {
					that.goodsList = res.data.data;



					that.paymentList = res.data.payment;
					console.log(res.data.jsondata)
					that.totalAmount = res.data.totalAmount;
					that.order_info.payment_id = res.data.payment[0].id;
					that.payType = res.data.payment[0].id;
					
					that.order_info.jsondata = res.data.jsondata;
					// jsondata:'[{ "article_id":0,"price":0,"spec_text":"","img_url":"","title":"", "goods_id":0, "quantity":0, "hot_id":0}]',
				} else {



				}



			}
		});
		//商品数据
		//let data = JSON.parse(option.data);
		//console.log(data);
	},
	methods: {
		SwitchA(e) {
			this.switchA = e.detail.value
		},
		//显示优惠券面板
		toggleMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(() => {
				this.maskState = state;
			}, timer)
		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type) {
			this.payType = type;
		},
		submit() {
			let that = this;
			console.log(that.order_info.address)
			// uni.redirectTo({ 
			// 	url: '/pages/money/pay'
			// })
			uni.request({
				url: common.order_saveUrl, //仅为示例，并非真实接口地址。
				data: {
					is_mobile: 1,
					user_id: that.userInfo.id,
					jsondata: that.order_info.jsondata,
					payment_id: that.payType,
					accept_name: that.order_info.accept_name,
					mobile: that.order_info.mobile,
					area: that.order_info.area,
					book_id: that.order_info.book_id,
					address: that.order_info.address,
					goods_order_type:1, 
					is_daifu:   that.switchA?1:0
				},
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					if (res.data.status == 1) {


						that.$api.msg(res.data.info);
						uni.redirectTo({
							url: res.data.url
						})
					} else {



						that.$api.msg(res.data.info);
					}



				}
			});
		},
		stopPrevent() {}
	}
}
